import React from 'react';
import FormikDescription from 'gui/components/formikDescription';

export interface ITicketDescriptionProps {
  name?: string;
  values?: any;
  loading?: boolean;
}

const ContactDescription = (props: ITicketDescriptionProps) => {
  return (
    <FormikDescription
      name={props.name}
      values={props.values}
      label={`הודעה`}
      loading={props.loading}
    />
  );
};

ContactDescription.defaultProps = {
  name: "message",
  values: {},
};

export default React.memo(ContactDescription);
