import {connect} from 'react-redux'
import {userSelector} from 'data/redux/selectors/userSelector';
import ContactForm from 'gui/pages/mainRouter/pages/contact/components/contactForm/ContactForm';
import {isLoadingSelector} from 'data/redux/selectors/guiSelector';
import {createSupportMessageActionCreator} from 'data/redux/actions/usersActions';

const mapStateToProps = (state) => {
  return {
    userDetails: userSelector(state) || {},
    loading: isLoadingSelector(state),
  };
};

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    onSubmit: (formValues) => dispatch(createSupportMessageActionCreator(formValues)),
    ...ownProps
  };
};

const ContactFormContainer = connect(mapStateToProps, mapDispatchToProps)(ContactForm);

export default ContactFormContainer;
