import React, {useState} from 'react';
import styles from './Contact.module.scss';
import {Container} from '@material-ui/core';
import ContactFormContainer from 'data/redux/containers/contactFormContainer';
import ContactSuccess from './components/contactsuccess';
import CrispChatContainer from 'data/redux/containers/crispChatContainer';

interface IContactProps {

}

const Contact = (props: IContactProps) => {
  const [state, setState] = useState(false);

  if (state === true) {
    setTimeout(() => {
      location.href = "/";
    }, 3000);
  }

  return (
    <Container className={`contactUsContainer ${styles.contactUsContainer}`} maxWidth="xs">
      <CrispChatContainer/>
      {state ?
        <ContactSuccess/>
        :
        <ContactFormContainer
          sent={() => setState(true)}
        />
      }
    </Container>
  );
};

export default Contact;
