import React from 'react';
import {Grid, Typography} from '@material-ui/core';
import {Form, Formik} from 'formik';
import * as Yup from "yup";
import FormikPhoneInput from 'gui/components/formikPhoneInput/FormikPhoneInput';
import FormikErrorScrollToTop from 'formik-top-error-focus';
import FormikTextInput from 'gui/components/formikTextInput';
import NextButton from 'gui/components/nextButton/NextButton';
import ContactDescription from './components/contactDescription';
import {ReactComponent as LoaderIcon} from 'assets/img/loader.svg';
import Recaptcha from 'gui/components/recaptcha/Recaptcha';

interface IContactFormProps {
  onSubmit: (formValues) => any;
  sent: (state: boolean) => void;
  userDetails?: any;
  loading?: boolean;
}

const yupSchema = Yup.object({
  email: Yup.string()
    .min(4, 'מייל קצר מדיי')
    .max(60, 'מייל ארוך מדיי')
    .email("מייל לא תקין")
    .required("נדרש מייל"),
  firstName: Yup.string()
    .min(2, "שם פרטי צריך להיות 2 אותיות לפחות")
    .max(15, "שם פרטי יכול להיות עד 15 אותיות")
    .required("נדרש שם פרטי"),
  lastName: Yup.string()
    .min(2, "שם משפחה צריך להיות 2 אותיות לפחות")
    .max(20, "שם משפחה יכול להיות עד 20 אותיות")
    .required("נדרש שם משפחה"),
  phone: Yup.string()
    .min(8, "יש להזין מספר נייד")
    .max(10, "יש להזין מספר נייד")
    .required("יש להזין מספר נייד"),
  message: Yup.string()
    .min(5, "הודעה קצרה מדיי")
    .max(10000, "הודעה ערוכה מדיי")
    .required("יש לרשום את ההודעה"),
});

const ContactForm = ({loading, userDetails, ...props}: IContactFormProps) => {
  return (<Formik
    initialValues={{
      email: userDetails.email || "",
      firstName: userDetails.firstName || "",
      lastName: userDetails.lastName || "",
      phone: userDetails.phone || "",
    }}
    validationSchema={yupSchema}
    onSubmit={async (values, {setSubmitting, setFieldError, setFieldTouched}) => {
      props.onSubmit(values)
        .payload
        .promise.then(s => {
        if (s.state === 200) {
          props.sent(true);
        }
      });
    }}
    enableReinitialize={true}
  >
    {({submitForm, isSubmitting, setFieldValue, values, touched, errors, submitCount, setFieldError}) => (
      <Form translate={null}>
        {loading ? <LoaderIcon

          /> :
          <Typography component="h1" variant="h5" style={{textAlign: "center", marginTop: 25, marginBottom: 25}}>
            צרו קשר
          </Typography>}

        <Grid container spacing={2}>
          <Grid item style={{marginTop: 15}} xs={12} sm={6}>
            <FormikTextInput
              label="שם פרטי"
              name="firstName"
              autoComplete="fname"
              values={values}
              errors={errors}
              touched={touched}
              loading={loading}
            />
          </Grid>
          <Grid item style={{marginTop: 15}} xs={12} sm={6}>
            <FormikTextInput
              label="שם משפחה"
              name="lastName"
              autoComplete="lname"
              values={values}
              errors={errors}
              touched={touched}
              loading={loading}
            />

          </Grid>
          <Grid item style={{marginTop: 15}} xs={12} sm={6}>
            <FormikTextInput
              label="מייל"
              name="email"
              autoComplete="email"
              values={values}
              errors={errors}
              touched={touched}
              setFieldError={setFieldError}
              onChange={(e) => {
                setFieldValue('email', (e.target.value || '').toLowerCase());
              }}
              loading={loading}
            />
          </Grid>
          <Grid item style={{marginTop: 15}} xs={12} sm={6}>
            <FormikPhoneInput
              label="מספר טלפון"
              name="phone"
              loading={loading}
            />
          </Grid>

          <Grid item style={{marginTop: 15}} xs={12}>
            <ContactDescription
              values={values}
              loading={loading}
            />
          </Grid>
          <Grid item xs={12}>
            <Recaptcha loading={loading}/>
          </Grid>
          <Grid item style={{marginTop: 5}} xs={12}>
            <NextButton
              isDisabled={isSubmitting}
              onClick={submitForm}
              loading={loading}
            >
              שלח
            </NextButton>
          </Grid>
        </Grid>
        <FormikErrorScrollToTop/>
      </Form>
    )}
  </Formik>);
};

ContactForm.defaultProps = {
  onSubmit: (formValues) => new Promise((resolve) => {
    
    setTimeout(resolve, 1000);
  }),
  sent: () => {
  },
  loading: false,
};

export default React.memo(ContactForm);
